@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap');

body {
    margin: 0; 
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #dfe2ff;   
}
  
* {
    box-sizing: border-box;    
    outline: none;
    font-family: 'Inter', sans-serif;
}
  
.App {
    width: 400px;
    height: 650px;
    margin: 50px auto;
    background-color: #fff;
    border-radius: 30px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
    padding: 40px;
}
  
.search {
    position: relative;
}
  
.search input {
    border: 1px solid rgba(0, 0, 0, 0.08);
    padding: 15px;
    border-radius: 15px;
    width: 100%;
    font-size: 16px;
    padding-left: 50px;
}
  
.search svg {
    width: 18px;
    height: 18px;
    position: absolute;
    top: 17px;
    left: 20px;
    opacity: 0.3;
}
  
.users-list {
    list-style: none;
    padding: 0;
    margin: 0;
    /* overflow: auto; */
    height: calc(100% - 130px);
    margin-top: 30px;
}
  
li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
}
  
.user-content {
    display: flex;
}
  
.avatar {
    border-radius: 30px;
    width: 50px;
    height: 50px;
    margin-right: 15px;
}
  
.action {
    width: 28px;
    height: 28px;
    opacity: 0.3;
    cursor: pointer;
}
  
.action:hover {
    opacity: 0.8;
}
  
.user-title {
    margin: 0;
    font-weight: 900;
    font-family: 'Merriweather', serif;
    margin-left: 15px;
    margin-top: 5px;
    margin-left: 0;
    color: #182170;
}
  
.user-text {
    display: flex;
    align-items: center;
    font-size: 14px;
    margin: 0;
    color: rgba(0, 0, 0, 0.5);
    margin-top: 3px;
}
  
.user-svg {
    width: 16px;
    height: 16px;
    margin-right: 4px;
}
  
.user-svg path {
    fill: rgba(0, 0, 0, 0.2);
} 
  
.send-invite-btn {
    background-color: rgb(251, 80, 0);
    padding: 16px 15px;
    border-radius: 15px;
    font-size: 16px;
    color: #fff;
    width: 100%;
    border: 0;
    outline: none;
    box-shadow: 0 7px 10px rgba(251, 80, 0, 0.4);
    cursor: pointer;
    transition: all 0.1s ease-in-out;
}
  
.send-invite-btn:hover {
    background-color: rgb(255, 97, 24);
}
  
.send-invite-btn:active {
    transform: translateY(2px);
    box-shadow: none;
}
  
.success-block {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex-direction: column;
    animation: success-show 0.5s cubic-bezier(0.25, 0.75, 0.5, 1.25);
}
  
.success-block img {
    width: 120px;
    height: 120px;
}
  
.success-block h3 {
    font-size: 24px;
    margin-bottom: 0;
    margin-top: 10px;
}
  
.success-block p {
    text-align: center;
    margin-bottom: 30px;
}
  
.success-block button {
    width: 200px;
}
  
.skeleton-list {
    height: calc(100% - 130px);
    margin-top: 30px;
}
  
.skeleton-list svg {
    margin-bottom: 15px;
}
  
@keyframes success-show {
    0% {
      opacity: 0;
      transform: scale(0.8);
    }
  
    100% {
      opacity: 1;
      transform: scale(1);
    }
}